<template>
  <div class="showContent checkInfoContent">
    <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
    <div class="box">
      <h3>Personal Information</h3>
      <a-form
        :layout="formLayout"
        @submit="submitInfor"
        :autoFormCreate="(form)=>{this.form = form}"
        :hideRequiredMark="true"
        class="checkInforForm"
      >
        <a-form-item
          label="Enter ID number"
          fieldDecoratorId="idNumber"
          :fieldDecoratorOptions="{rules: [{ required: true,whitespace:true, message: 'please enter' }]}"
          :labelCol="{span:6}"
          :wrapperCol="{span:18}"
        >
          <a-input>
            <!-- <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/> -->
          </a-input>
        </a-form-item>
        <a-form-item
          label="Name"
          fieldDecoratorId="name"
          :fieldDecoratorOptions="{rules: [{ required: true,whitespace:true, message: 'please enter'}]}"
          :labelCol="{span:4}"
          :wrapperCol="{span:20}"
        >
          <a-input>
            <!-- <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/> -->
          </a-input>
        </a-form-item>
        <a-form-item class="rightItem">
          <a-button>Reset</a-button>
          <a-button type="primary" htmlType="submit">Check</a-button>
        </a-form-item>
      </a-form>
    </div>
    <p class="result" v-if="checkResult">Result</p>
    <div class="box" v-if="checkResult">
      <div class="infor_form">
        <div>
          <!-- 公司名 -->
          <label>Name </label>
          <input type="text" :value="checkResult.name" readonly>
        </div>
        <div>
          <!-- ID -->
          <label>ID Number</label>
          <input type="text" :value="checkResult.idNumber" readonly>
        </div>
        <div>
          <!-- Province -->
          <label>Province</label>
          <input type="text" :value="checkResult.province" readonly>
        </div>
        <div>
          <!-- city -->
          <label>City</label>
          <input type="text" :value="checkResult.city" readonly>
        </div>
        <div>
          <!-- district -->
          <label>District</label>
          <input type="text"  :value="checkResult.district" readonly>
        </div>
        <div>
          <!-- village -->
          <label>Village</label>
          <input type="text"  :value="checkResult.village" readonly>
        </div>
        <div>
          <!-- village -->
          <label>Name Similar</label>
          <input type="text" :value="checkResult.nameSimilar"  readonly>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
export default {
  data() {
    return {
      routekey: ["checkPersonInfo", "personInfoHistory"],
      formLayout: "inline",
      checkResult:"",
    };
  },
  components: {
    requestAndHisSubMenu
  },
  methods: {
    submitInfor(e) {
        console.log(111);
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log(values);
          this.checkResult="";
          this.$store.dispatch("personCheck", values).then(
            res => {
              console.log(res);
              this.checkResult=res.data;
              this.checkResult.nameSimilar=(this.checkResult.nameSimilar)*100 +"%"
            },
            error => {
              console.log(error);
              if(error.code==400){  //ID无效  Id Number is invalid
              this.$message.error('身份证号码无效');
              }
            }
          );
        }
      });
    },
  }
};
</script>
<style lang="less">
@import "../../../css/checkInfor.less";
</style>




